import http from "./http";
import list from "./httpurl"
import config from "../appData"
var VueCookie = require('vue-cookie');

// axios.defaults.baseURL = `${config.host}/`;
var data ={};
list.forEach(val=>{
    data[val.name] = (param)=>{
        param.url=`api/${val.url}`
        getDatafunc(param)
    }
})
function getDatafunc(param){
   var d_t =  testcook();
   if(!d_t){
       console.log(`${config.host}/login.html`)
        // window.location.href =`${config.host}/login.html`
   }
    http.post_data({
        url:param.url,
        data:param.data||{},
        success:function(res){
            console.log(res)
            typeof param.success =="function" &&param.success(res.data)
        },
        error:function(res){
            console.log(res)
            typeof param.error =="function" &&param.error(res.data)
        }
    })

}
data.go=(param)=>{
var that = param.that,
url = param.url||'404',
data =param.data||{};
console.log(data)
    that.$router.push(
        {
            path:url,
            query:data
        }
  )
}

function testcook(){
   return VueCookie.get('d_t')
}


export default data;


