<template>
  <div class="hello">
     <p class="page-title">当前位置:销售商管理-待审核详情</p>
     <div class="list"><span>真实姓名 ：</span>{{project.userName}}</div>
     <div class="list"><span>手机号：</span> {{project.phoneNumber}}</div>
     <div class="list"><span>身份证号：</span> {{project.identity}}</div>
     <div class="list"><span>联系地址：</span> {{project.address}}</div>
     <div class="list"><span>身份证正面：</span></div>
     <div class="imgboix">
            <div><img @click="showimgfunc(project.pic1)" :src="project.pic1" alt="">
                <p>正面照</p>
            </div>
            <div><img @click="showimgfunc(project.pic2)" :src="project.pic2" alt="">
                <p>反面照</p>
            </div>

     </div>

     <div class="list-btn">
            <Button type="primary"@click="ok(1)">通过审核</Button><br><br>
            <Button type="primary"@click="ok(2)">不通过审核</Button>
     </div>
     <div>
         &nbsp &nbsp 不通过审核原因：
         <RadioGroup v-model="animal">
        <Radio label="1">身份证照片不清晰</Radio>
        <Radio label="2">身份证号与身份证不匹配</Radio>
    </RadioGroup>
     </div>
     
 <Modal title="View Image" v-model="visible">
        <img :src="showimg" v-if="showimg"  style="width: 100%">
    </Modal>

  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
        showimg:"",
        visible:false,
        animal:'1',
        project:{},
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods:{
      showimgfunc(d){
          this.showimg = d;
            this.visible = true
      },
      ok(d){
          var self = this;
           var data ={id:this.project.id};
                if(d==1){
                    data.status=1
                }else{
                    data.status=2
                    data.reason=this.animal
                }

            this.$api.backupdataSale({
                data:data,
                success:res=>{
                    self.$router.back(-1)
                }
            })
            // 1通过
            // 2不通过

      },
  },
  mounted(){
        var query = this.$route.query.data;
        this.project = JSON.parse(query)
        console.log(query)



  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.list{
    margin: 20px auto;
    /* padding-left: 20px; */
}
.imgboix{
    width:500px ;
    display: flex;
    justify-content:space-between;

    margin: 0   0 0 100px;
}
.imgboix div{
    width: 200px;
}
.imgboix p{
    text-align: center;
    margin-top: 10px;
}
.imgboix img{
    width: 200px;
    height: 100px;
}
.list span{
    width: 100px;
    text-align: right;
    display: inline-block;
}
.list-btn{
    padding-left: 100px;
    margin: 20px auto;

}
</style>



