
import HelloWorld from '@/components/HelloWorld'
import TheSale from '@/components/TheSale'
import SaleInfo from '@/components/SaleInfo'
export default 
  [
    {
      path: '/',
      name: 'app',
      title:"首页",
      component:  () => import('@/components/app')
    },
    {
      path: '/sale',
      name: 'sale',
      component: TheSale
      
    },
    {
      path: '/SaleInfo',
      name: 'sale',
      component: SaleInfo
      
    },
    {
      path: '/provide',
      name: 'provide',
      component: HelloWorld
    },
    {
      path: '/zhanghu',
      name: 'zhanghu',
      component:  () => import('@/components/user')
    },
    {
      path: '/quanxian',
      name: 'quanxian',
      component:  () => import('@/components/user/quanxian')
      
    },
    {
      path: '/juese',
      name: 'juese',
      component:  () => import('@/components/user/juese')
    },
    {
      path: '/Sale_ifo',
      name: 'Sale_ifo',
      component:  () => import('@/components/Sale_ifo')
    },
    
    {
      path: '/jsdd',
      name: 'jsdd',
      component: HelloWorld
    },
    {
      path: '/totsgl',
      name: 'totsgl',
      component:  () => import('@/components/totsgl')
    },
    {
      path: '/tosudetai',
      name: 'tosudetai',
      component:  () => import('@/components/totsgl/tosudetai')
    },
    
    {
      path: '/ddgl',
      name: 'ddgl',
      component: HelloWorld
    },
    {
      path: '/ptyh',
      name: 'ptyh',
      component:  () => import('@/components/user/ordinary')
    },
    {
      path: '/ordinary_ifo',
      name: 'ordinary_ifo',
      component:  () => import('@/components/user/ordinary_ifo')
    },
    
    
  ]
 


