<template>
  <div id="app">
    <div class="left">
 <Menu theme="dark" width ="200" @on-select="changesa" :active-name="active">
            <MenuItem name="1"  to='/'>
            <Icon type="md-pricetags" />
                首页
            </MenuItem>
            <MenuItem name="2" to='sale'>
            <Icon type="ios-keypad" />
                销售商管理
            </MenuItem>
            <MenuItem name="3" to='provide'>
            <Icon type="md-list" />
                供应商管理
            </MenuItem>
             <MenuItem name="4" to="ptyh">
            <Icon type="md-list" />
                普通用户
            </MenuItem>
             <MenuItem name="5" to="jsdd">
            <Icon type="md-list" />
                结算订单
            </MenuItem>
             <MenuItem name="6" to="ddgl">
            <Icon type="md-list" />
                订单管理
            </MenuItem>
             <MenuItem name="8" to='totsgl'>
            <Icon type="md-list" />
                投诉管理
            </MenuItem>
             <MenuItem name="9"  to="juese">
            <Icon type="md-list" />
                角色管理
            </MenuItem>
             <MenuItem name="10" to="quanxian">
            <Icon type="md-list" />
                权限管理
            </MenuItem>
             <MenuItem name="11" to="zhanghu">
                <Icon type="ios-cafe" />
                账户管理
            </MenuItem>
             <MenuItem name="12" >
                <Icon type="ios-cafe" />
                退出登录
            </MenuItem>
    </Menu>
    </div>
  <div class="right">
    <div class="flex sbts">
     <Icon type="md-home" size="20" class="cursor" @click="goto" />
      <div class="user"><Icon type="md-person" />anmin</div>
    </div>
      <router-view/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return{
      active:"1"
    }
  },
  watch:{
    active(val){
      console.log(val);
      sessionStorage.setItem('active',val)
    }
  },
  mounted(){
     this.active =  sessionStorage.getItem('active')||'1'

  },
  methods:{
    changesa(val){
      console.log(val);
      if(val==12){

            this.$Modal.confirm({
                    title: '确定退出',
                    onOk: () => {
                     this.$cookie.delete('test');
                    },
                    onCancel: () => {
                         this.active =  sessionStorage.getItem('active')||'1'
                    }
                });
      }else{
      sessionStorage.setItem('active',val)

      }
    },
    goto(){
      this.$router.push("/")
                         this.active =  '1'

    }
  }
}
</script>

<style>
html,
body{
  padding: 0;
  margin: 0;
  height: 100%;

}
.cursor{
cursor: pointer;
}

#app {
  display: flex;
  height: 100%;
  width: 100%;
}
.left{
  width: 200px;
  height: 100%;
  background-color: rgb(80, 90, 110);
}
.right{
  width:  1000px;
  padding: 5px 20px;
}
.flex{
  display: flex;
}
.sbts{
justify-content:space-between;
}
.pages{
  text-align: right;
  margin: 10px auto;
}
</style>



