<template>
  <div class="hello">
      <p class="page-title">当前位置:销售商管理</p>
        <div class="search">
            <Input v-model="userName" placeholder="按姓名" style="width: 150px" />
            <Input v-model="phoneNumber" placeholder="手机号" style="width: 150px;margin:0 20px;" />
            <Button type="primary">搜索</Button>
        </div>

      <div class="nav-tab">
          <span :class="{on:on==1}"@click="tabclick(1)">已审核</span>
          <span :class="{on:on==2}"@click="tabclick(2)">待审核</span>
      </div>
       <Table v-show="on==1" border :columns="columns7" :data="data"></Table>
       <Table v-show="on==2" border :columns="columns6" :data="data"></Table>
  </div>
</template>

<script>
let self =null;
export default {
  name: 'seal',
  data () {
    return {
      on:1,
      userName:"",
      phoneNumber:"",
      page:1,
      data:[],
      msg: 'Welcome to Your Vue.js App',
       columns7: [
                    {
                        title: '姓名',
                        key: 'userName',
                    },
                    {
                        title: '手机号',
                        key: 'phoneNumber'
                    },
                    {
                        title: '拼单量',
                        key: 'completenum'
                    },
                     {
                        title: '拼单金额',
                        key: 'completemoney'
                    },
                    {
                        title: '操作',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('span', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            var data =JSON.stringify(params.row)

                                              this.$api.go({
                                                that:this,
                                                url:"Sale_ifo",
                                                data:{data:data}
                                            })
                                        }
                                    }
                                }, '查看')
                            ]);
                        }
                    }
                ],
                   columns6: [
                    {
                        title: '姓名',
                        key: 'userName',
                    },
                    {
                        title: '手机号',
                        key: 'phoneNumber'
                    },
                    {
                        title: '拼单量',
                        key: 'completenum'
                    },
                     {
                        title: '拼单金额',
                        key: 'completemoney'
                    },
                    {
                        title: '操作',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('span', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            var data =JSON.stringify(params.row)
                                            this.$api.go({
                                                that:this,
                                                url:"SaleInfo",
                                                data:{data:data}
                                            })
                                        }
                                    }
                                }, '审核')
                            ]);
                        }
                    }
                ],
                data7:[
                     {
                        name: 'John Brown',
                        age: 18,
                        address: 'New York No. 1 Lake Park'
                    },
                    {
                        name: 'Jim Green',
                        age: 24,
                        address: 'London No. 1 Lake Park'
                    },
                    {
                        name: 'Joe Black',
                        age: 30,
                        address: 'Sydney No. 1 Lake Park'
                    },
                    {
                        name: 'Jon Snow',
                        age: 26,
                        address: 'Ottawa No. 2 Lake Park'
                    }
                ],
                data:[],
                data6: [
                   
                ]
            
    }
  },
  watch:{
      on(on){
          return;
          console.log(self.data6)
          var arr = [];
          if(self.data6.length==0){
              this.data=[]
              return;
          } 
          self.data6.forEach(val => {

               if(on==1){
                if(val.status>0){
                        arr.push(val)

                }
                }else{
                    if(val.status==0){
                        arr.push(val)

                }
                }
              
          });
              this.data= arr;
      },
  },

  mounted(){
      self = this;
        this.initPage()
  },
  methods:{

      initPage(){
            this.$api.getSale({
                data:{
                    type:this.on,
                    page:this.page},
                success:function(res){
                      self.data = res.data.list  
                }
            })
      },
      tabclick(d){
          this.on=d
        this.initPage()
      },
      show (index) {
                this.$Modal.info({
                    title: 'User Info',
                    content: `Name：${this.data6[index].name}<br>Age：${this.data6[index].age}<br>Address：${this.data6[index].address}`
                })
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



